@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:400,700&display=swap);
html, body {
  height: 100%;
  margin: 0;
  padding: 0; }

@font-face {
  font-family: LeagueMonoVariable;
  src: url(/fat/static/media/LeagueMonoVariable.36660314.ttf) format("truetype"); }

body {
  margin: 0;
  font-family: LeagueMonoVariable, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.wrapper {
  margin: 0 auto;
  height: 100%;
  width: 572px;
  position: relative; }
  .wrapper svg.chart {
    width: 572px;
    height: 702px; }
  .wrapper svg.footer {
    margin-left: 26px; }

.label {
  font-variation-settings: "wdth" 50; }
  .label.withoutData {
    opacity: 0.2;
    font-variation-settings: "wght" 400, "wdth" 50 !important; }

#tooltip {
  position: absolute;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 100px;
  background: #000000;
  opacity: 0.8;
  pointer-events: none; }
  #tooltip.hidden {
    display: none; }
  #tooltip p {
    color: white;
    margin: 0;
    font-size: 10px;
    line-height: 13px;
    padding: 4px 9px 5px; }
  #tooltip .year {
    opacity: 0.4; }

#root {
  background: url(/fat/static/media/bg.3aba9a7d.png);
  background-size: contain;
  margin-bottom: -50px; }

.header {
  font-family: 'IBM Plex Mono', monospace;
  -webkit-transform: translate(0, 33px);
          transform: translate(0, 33px); }
  .header .main {
    font-family: IBM Plex Mono;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 100%; }
  .header .legend, .header .note {
    font-family: IBM Plex Mono;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 120%; }
  .header .percents {
    font-family: LeagueMonoVariable; }

